import interfaceurl from '@/utils/interface.js'
import navigationBar from '@/components/navigation-bar.vue' //引入自定义导航栏
export default {
	components: {
		navigationBar
	},
	data() {
		return {
			//设置导航栏样式
			navigationBarStyle: {
				iconText: '我的团队' //导航栏文字
			},
			teamData: {}, //团队数据
			teamList: [], //团队列表
			params: {
				page: 1, //页数
				size: 10, //每页几条
				type: 1 //1:一级用户2:一级会员用户3二级用户4:二级会员用户
			}, //分页参数
		}
	},
	onShow() {
		this.initData()
	},
	//到达页面底部时触发的事件
	onReachBottom() {
		if (this.teamList.length >= this.teamData.total) {
			return;
		}
		this.params.page++;
		this.getTeamList()
	},
	methods: {
		//切换一级和二级粉丝
		setType(type) {
			this.params.type = type
			this.initData()
		},
		initData() {
			//重置分页参数
			this.teamData = {}
			this.teamList = []
			this.params.page = 1
			this.getTeamList()
		},
		//获取一级粉丝和二级粉丝
		getTeamList() {
			let that = this;
			interfaceurl.checkAuth(interfaceurl.teamPageList, this.params, false).then((res) => {
				that.teamData = res.data
				if(that.params.page == 1) {
					that.teamList = res.data.data
				} else {
					that.teamList = that.teamList.concat(res.data.data)
				}
			});
		}
	}
}
